@font-face{
  font-family: 'RobotoMono';
  src: url(../../static/media/Roboto-Thin.d1e42f1b.ttf);
}

*{
  font-family: 'RobotoMono', monospace;
}

body{
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  text-align: center;

  color: white;
  background: -webkit-gradient(linear, left top, right top, from(rgb(83, 105, 118)), to(rgb(41, 46, 73)));
  background: linear-gradient(to right, rgb(83, 105, 118), rgb(41, 46, 73))
}
.calculator{
    height: 320px;
    width: 235px;
    border-radius: 5px;
    overflow: hidden;

    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: 1fr 48px 48px 48px 48px 48px;
}
:root{
    --bg-button: #f0f0f0;
    --border-button: solid 1px #888;
}

.button{
    font-size: 1.4em;
    background-color: #f0f0f0;
    background-color: var(--bg-button);
    border: none;
    border-right: solid 1px #888;
    border-right: var(--border-button);
    border-bottom: solid 1px #888;
    border-bottom: var(--border-button);
    outline: none;

}

.button:active{
    background-color: #ccc;
}

.button.double{
    grid-column: span 2;
}

.button.triple{
    grid-column: span 3;
}

.button.operation{
    background-color: #fa8231;
    color: #FFF;
}

.button.operation:active{
    background-color: #fa8231cc;
}
.display{
    grid-column: span 4;
    background-color: #0004;

    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 20px;
    font-size: 2.2em;
    overflow: hidden;
}

